body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,h2,h3,h4,h5,span,p{
  color: #fff;
}

h1{
  font-size: 22px;
  font-weight: 500;
}

h2{
  font-size: 16px;
  font-weight: 300;
}

h3{
  font-size: 22px !important;
}

.tupper{
  text-transform: uppercase;
}

.fs32{
  font-size: 32px;
}

.mainsec{
  width: 100%;
  height: 100vh;
  background: url('./assets/back-cover.jpg') center center no-repeat ;
  background-size: cover;
  position: relative;
}

.sidenav{
  position: absolute;
  top: 0;
  left: 0;
  width: 25%;
  height: 100vh;
  background-color: rgba(145,13,13,.8);
}

.myimg{
  width: 100%;
  height: auto;
}

.c-red{
  color: rgba(145,13,13);
}


.images{
  width: 100%;
  height: auto;
  border-radius: 50%;
}

.profile{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 18%;
}

.rounded-circle {
  border-radius: 50% !important;
  width: 180px;
  height: 170px;
}

.mob-items{
  position: fixed;
  top: 49px;
  width: 376px;
  height: 140px;
  left: 0px;
  background-color: rgba(145,13,13,.8);
}

.mob-items ul{
  text-decoration: none;
  list-style: none;
  line-height: 35px;
  margin-top: 17px; 
}

.mob-items ul li{
  color: rgba(255,255,255,.6);
  text-decoration: none;
  font-size: 16px;
  font-weight: 300;
}

.nav-linkss ul{
  text-decoration: none;
  list-style: none;
  padding-left: 59px;
  line-height: 35px;
  width: fit-content;
}

.nav-linkss ul li{
  color: rgba(255,255,255,.6);
  text-decoration: none;
  font-size: 16px;
  font-weight: 300;
}

.nav-linkss ul li:hover{
  color: #fff;
  cursor: pointer;
}

.nav-linkss ul li:hover::after{
    content: " \f105 ";
    padding-left: 3px;
    font-family: FontAwesome;
}

.block-title {
  font-size: 11px;
  font-weight: 300;
  color: #aaa;
  color: rgba(255,255,255,.5);
  margin-bottom: 15px;
}

.contactme{
  padding-left: 59px;
}

.social-icons.social-icons a{
  color: rgba(255,255,255,.6);
  text-decoration: none;
  font-size: 22px;
  padding-right: 10px;
}

.social-icons.social-icons a:hover{
  color: #fff;
}


.home-section-inner {
  max-width: 840px;
  margin-left: 25%;
  padding-left: 110px;
  padding-top: 130px;
}

.home-heading{
    color: #fff;
    text-transform: uppercase;
    font-size: 4em;
    font-weight: 500;
    text-shadow: 1px 1px 2px rgb(0 0 0 / 60%);
}

.home-subheading{
    font-size: 2em;
    font-weight: 300;
    color: #fff;
    text-shadow: 1px 1px 2px rgb(0 0 0 / 60%);
    line-height: 50px;
}
a{
  background: 0 0;
  list-style: none;
  text-decoration: none;
  color: rgba(255,255,255,.6);
}

a:hover{
  color: #fff;
}

a:hover{
  list-style: none;
  text-decoration: none;
}

.button-outline {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-transform: uppercase;
  font-weight: 300;
  text-decoration: none;
  box-shadow: 1px 1px 2px rgb(0 0 0 / 60%);
  margin-bottom: 1em;
  text-decoration: none;
  color: #fff;
  border-width: 1px;
  border-style: solid;
  padding: .4em .8em;
  line-height: 1;
  margin-right: 1em;
  display: flex;
  font-size: 2em;
  width: fit-content;
  opacity: 0.8;
}

.button-outline:hover{
  color: #c0c0c0 ;
}

.c-black{
  color: #555;
}

.MyPorfolio{
    margin-left: 25%;
    height: 92vh;
    background-color: #fff;
}

.bdrb{
  border-bottom: 2px solid #000;
}

.Projects{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 210px;
}


.imagediv img{
  -webkit-transform: scale(1);
	transform: scale(1);
	-webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
}

.imagediv img:hover{
  -webkit-transform: scale(1.3);
	transform: scale(1.04);
}



@media screen and (max-width: 560px) {
  .home-section-inner {
    max-width: unset;
    margin-left: unset;
    padding-left: 28px;
    padding-top: 300px;
  }

  .home-heading{
    font-size: 25px;
  }

  .MyPorfolio{
    margin-left: unset;
    height: 92vh;
    background-color: #fff;
}

  .home-subheading {
    font-size: 18px;
    line-height: 30px;
  }

  .mobnav{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 111;
    height: 3rem;
    background-color: rgba(145,13,13,.8);
  }

  .contactme{
    padding-left: unset;
  }

  .c-white{
    color: #fff;
  }

  .button-outline{
    opacity: unset;
    font-size: 18px;
  }

  #react-burger-menu-btn{
    position: absolute !important;
    right: 0 !important;
    top: 0 !important;
    left: unset !important;
    z-index: 1 !important;
    width: 20% !important;
    height: 100% !important;
    margin: 0px !important;
    padding: 0px !important;
    border: none !important;
    font-size: 12px !important;
    background: transparent !important;
    cursor: pointer !important;
  }

  .Projects{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 260px;
  }
}




